import React from 'react'
import { withRouter, Route, Switch } from 'react-router-dom'
import { connect } from 'react-redux'

import Auth from './authentication/Auth'
import CreateQuiz from './admin/CreateQuiz'
import ShowQuiz from './user/ShowQuiz'
import Setting from './user/Setting'
import ListQuizCreated from './admin/ListQuizCreated'
import QuizEdit from './admin/QuizEdit'
import QuizsetEdit from "./admin/QuizsetEdit"
import CreateQuizset from "./admin/CreateQuizset"
import ListQuizset from "./admin/ListQuizset"
import Scoreboard from './user/Scoreboard'
import FourOFour from './authentication/FourOFour'
import ShowQuizset from "./user/showQuizset";

function AdminRoutes() {
  return (
    <>
      <Route exact path='/admin/quizzes' component={ListQuizCreated} />
      <Route exact path='/admin/quiz/create' component={CreateQuiz} />
      <Route exact path='/admin/quiz/update' component={CreateQuiz} />
      <Route exact path='/admin/quiz/edit/:id' component={QuizEdit} />
      <Route exact path="/admin/quizset/new" component={CreateQuizset}/>
      <Route exact path="/admin/quizsets" component={ListQuizset} />
      <Route exact path="/admin/quizset/edit/:id" component={QuizsetEdit} />
    </>
  )
}

function UserRoutes() {
  return (
    <>
      <Route exact path='/user/quizzes/:name/:id' component={ShowQuiz} />
      <Route exact path='/user/quizsets' component={ShowQuizset} />
      <Route exact path='/user/scoreboard' component={Scoreboard} />
      <Route exact path='/user/setting' component={Setting} />
    </>
  )
}

class App extends React.Component {
  constructor() {
    super()
    this.state = {}
  }

  render() {
    return (
      <Switch>
        <Route exact path='/auth' component={Auth} />

        {localStorage.user === 'admins' ? (
          <AdminRoutes />
        ) : localStorage.user === 'users' ? (
          <UserRoutes />
        ) : this.props.user === null && this.props.location.pathname == '/' ? (
          <Auth />
        ) : (
          <FourOFour />
        )}
      </Switch>
    )
  }
}
function mapStateToProps(state) {
  return state.user
}

export default connect(mapStateToProps)(withRouter(App))
