import { SET_QUIZSET, DEL_QUIZSET } from '../types'

let INITIAL_STATE = {
  quizSet: null
}

function qsetReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case SET_QUIZSET:
      console.log(action.payload)
      return {
        ...state,
        quizSet: action.payload
      }

    case DEL_QUIZSET:
      return {
        ...state
      }

    default:
      return state
  }
}

export default qsetReducer
