import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import Header from './Header'

class ShowQuiz extends React.Component {
  constructor() {
    super()
    this.state = { result: [] }
  }

  componentDidMount() {
    this.fetchQuiz()
  }

  fetchQuiz = () => {
    fetch('/api/v1/quizzes/quizset/' + this.props.match.params.id)
      .then(res => res.json())
      .then(res => {
        if (res.success) {
          this.setState({ quizList: [...res.listQuestions.questions] })
          const result = []
          this.state.quizList.forEach((val, ind) => {
            result.push(null)
          })
          this.setState({ result: result })
        }
      })
      .catch(err => alert(err))
  }

  handleMark = (val, ind) => {
    this.state.result[ind] = val
    this.setState({ result: [...this.state.result] })
  }

  handleSubmit = event => {
    fetch('/api/v1/users/score', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      },
      body: JSON.stringify({
        quizset_id: this.props.match.params.id,
        ...this.state
      })
    })
      .then(res => res.json())
      .then(res => {
        if (res.success) {
          this.props.history.push('/user/scoreboard')
        }
      })
      .catch(err => alert(err))
  }

  render() {
    return (
      <>
        <Header />
        <div className='wrapper'>
          <h2 className='quizset-text-top'>{this.props.match.params.name}</h2>
          <div className='quizzes-container'>
            <div className='quiz-card'>
              {this.state.quizList ? (
                this.state.quizList.map((val, ind) => (
                  <>
                    <div className='card'>
                      <span>{val.question}</span>
                      <br />
                      <br />
                      <select
                        onChange={event => {
                          this.handleMark(event.target.value, ind)
                        }}
                        className='quizset'
                      >
                        <option className='select' value={val.option1}>
                          {val.option1}
                        </option>
                        <option className='select' value={val.option2}>
                          {val.option2}
                        </option>
                        <option className='select' value={val.option3}>
                          {val.option3}
                        </option>
                        <option className='select' value={val.option4}>
                          {val.option4}
                        </option>
                      </select>
                    </div>
                    <br />
                  </>
                ))
              ) : (
                <center>No Quiz Found</center>
              )}
              {this.state.quizList ? (
                <button onClick={this.handleSubmit} className='btn'>
                  Submit
                </button>
              ) : (
                ''
              )}
            </div>
          </div>
        </div>
      </>
    )
  }
}

export default withRouter(ShowQuiz)
