import React from 'react'
import Header from './Header'

class Scoreboard extends React.Component {
  constructor() {
    super()
    this.state = {}
  }

  componentDidMount() {
    fetch(`/api/v1/users/score`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      }
    })
      .then(res => res.json())
      .then(res => {
        if (res.success) {
          this.setState({ data: res.scoreboard.scoreboard })
        }
      })
  }

  render() {
    return (
      <>
        <Header />
        <div className='wrapper'>
          <h1 className='quizset-text-top'>Scoreboard</h1>
          <br/>
          <br/>
          <div className='table-container'>
            <span>
              <div className='table-head'>Category</div>
              {this.state.data ? (
                this.state.data.map((val, ind) => (
                  <div className='table-data' key={ind}>
                    {val.category}
                  </div>
                ))
              ) : (
                <center>No Data Found</center>
              )}
            </span>

            <span>
              <div className='table-head'>Score</div>
              {this.state.data ? (
                this.state.data.map((val, ind) => (
                  <div className='table-data' key={ind}>
                    {val.score}
                  </div>
                ))
              ) : (
                <center>No Data Found</center>
              )}
            </span>

            <span>
              <div className='table-head'>Out Of</div>
              {this.state.data ? (
                this.state.data.map((val, ind) => (
                  <div className='table-data' key={ind}>
                    {val.outof}
                  </div>
                ))
              ) : (
                <center>No Data Found</center>
              )}
            </span>

            <span>
              <div className='table-head'>Date</div>
              {this.state.data ? (
                this.state.data.map((val, ind) => (
                  <div className='table-data' key={ind}>
                    {val.date.split("T")[0]}
                  </div>
                ))
              ) : (
                <center>No Data Found</center>
              )}
            </span>
          </div>
        </div>
      </>
    )
  }
}

export default Scoreboard
