import React from 'react'
import { withRouter } from 'react-router-dom'

import AdminHeader from './AdminHeader.jsx'

class CreateQuizset extends React.Component {
  constructor() {
    super()
    this.state = { quizset: '' }
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value })
  }

  handleSubmit = event => {
    fetch(`/api/v1/quizset`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      },
      body: JSON.stringify({
        name: this.state.quizSet
      })
    })
      .then(res => res.json())
      .then(res => {
        if (res.success) {
          alert(res.msg)
          this.props.history.push("/admin/quizsets")
        }
      })
  }

  render() {
    return (
      <>
        <AdminHeader />
        <div className='wrapper'>
          <h2 className='createnew'>Create New Quiz set</h2>
          <div className='create-quiz'>
            <label htmlFor='' className='crate-new-label'>
              Quiz Set Name:
            </label>
            <input
              type='text'
              name='quizset'
              onChange={this.handleChange}
              className='input-classic'
            />
            <button className="create-btn" onClick={this.handleSubmit}>Create</button>
          </div>
        </div>
      </>
    )
  }
}


export default withRouter(CreateQuizset)
