import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { fetchQuizzes, fetchUsers, fetchQuizset } from '../../actions'

import Header from './Header'

class ShowQuizset extends React.Component {
  constructor() {
    super()
    this.state = {}
  }

  componentDidMount() {
    this.props.dispatch(fetchQuizset())
  }

  handlePlay = (name, id) => {
    this.props.history.push('/user/quizzes/' + name + '/' + id)
  }

  render() {
    return (
      <>
        <Header />
        <div className='wrapper'>
          <div className='quizset-container'>
            {this.props.allQuizset ? (
              this.props.allQuizset.map(obj => (
                <div className='card quizset-card'>
                  <span className='quizset-text'>{obj.name}</span>
                  <span>
                    <button
                      className='delete'
                      onClick={() => {
                        this.handlePlay(obj.name, obj._id)
                      }}
                    >
                      P L A Y
                    </button>
                  </span>
                </div>
              ))
            ) : (
              <center>Loading...</center>
            )}
          </div>
        </div>
      </>
    )
  }
}

function mapStateToProps(state) {
  return state.qset.quizSet
}

export default connect(mapStateToProps)(withRouter(ShowQuizset))
