import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import { fetchQuizset } from '../../actions'

class QuizEdit extends React.Component {
  constructor() {
    super()
    this.state = {
      question: '',
      quizset: '',
      option1: '',
      option2: '',
      option3: '',
      option4: '',
      answer: ''
    }
  }

  setQuizSet = event => {
    let index = event.target.value
    this.setState({
      quizset_id: this.props.allQuizset[index]._id,
      quizset: this.props.allQuizset[index].name
    })
  }

  handleSubmit = event => {
    event.preventDefault()
    fetch(`/api/v1/quizzes/${this.props.match.params.id}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      },
      body: JSON.stringify({
        question: this.state.question,
        quizset: this.state.quizset,
        quizset_id: this.state.quizset_id,
        option1: this.state.option1,
        option2: this.state.option2,
        option3: this.state.option3,
        option4: this.state.option4,
        answer: this.state.answer
      })
    })
      .then(res => res.json())
      .then(res => {
        if (res.msg) {
          alert(res.msg)
          this.props.history.push('/admin/quizzes')
        }
      })
  }

  componentDidMount() {
    this.props.dispatch(fetchQuizset())

    fetch(`/api/v1/quizzes/${this.props.match.params.id}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      }
    })
      .then(res => res.json())
      .then(res => this.setState(res))
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value })
  }

  render() {
    return (
      <div className='wrapper'>
        <div>
          <form onSubmit={this.handleSubmit} className='create-quiz'>
            <label className='crate-new-label' htmlFor='question'>
              Question:
            </label>
            <input
              className='crate-new-label'
              type='text'
              name='question'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.question}
            />
            <label className='crate-new-label' htmlFor='quizset'>
              Quizset:
            </label>
            <select
              name='quizset'
              onChange={this.handleChange}
              className='select-answer'
              value={this.state.quizset}
            >
              {this.props.allQuizset ? (
                this.props.allQuizset.map((val, ind) => (
                  <option value={ind} data-id={val._id} key={ind}>
                    {val.name}
                  </option>
                ))
              ) : (
                <option>Select</option>
              )}
            </select>
            <br />
            <label className='crate-new-label' htmlFor='option1'>
              Option1:
            </label>
            <input
              type='text'
              name='option1'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.option1}
            />
            <label className='crate-new-label' htmlFor='option2'>
              Option2:
            </label>
            <input
              type='text'
              name='option2'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.option2}
            />

            <label
              className='crate-new-label'
              htmlFor='option3'
              onChange={this.handleChange}
            >
              Option3:
            </label>
            <input
              type='text'
              name='option3'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.option3}
            />
            <label className='crate-new-label' htmlFor='option4'>
              Option4:
            </label>
            <input
              type='text'
              name='option4'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.option4}
            />
            <label className='crate-new-label' htmlFor='answer'>
              Answer:
            </label>
            <input
              type='text'
              name='answer'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.answer}
            />
            <button className='btn' type='submit'>
              Submit
            </button>
          </form>
        </div>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return state.qset.quizSet
}

export default connect(mapStateToProps)(withRouter(QuizEdit))
