import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { fetchQuizset } from '../../actions'
import AdminHeader from './AdminHeader.jsx'

class ListQuizset extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  componentDidMount() {
    this.props.dispatch(fetchQuizset())
  }

  handleEditQuizset = name => {
    this.props.history.push("/admin/quizset/edit/" + name)
  }

  handleDeleteQuizset = name => {}

  render() {
    return (
      <>
        <AdminHeader />
        <div className='wrapper'>
          <div>
            {this.props.allQuizset ? (
              this.props.allQuizset.map(obj => (
                <div className='card quizset-card'>
                  <span className='quizset-text'>{obj.name}</span>
                  <span>
                    <button
                      className='delete'
                      onClick={() => {
                        this.handleEditQuizset(obj.name)
                      }}
                    >
                      Edit
                    </button>
                    <button
                      className='delete'
                      onClick={() => {
                        this.handleDeleteQuizset(obj.name)
                      }}
                    >
                      Delete
                    </button>
                  </span>
                </div>
              ))
            ) : (
              <center>Loading...</center>
            )}
          </div>
        </div>
      </>
    )
  }
}

function mapStateToProps(state) {
  return state.qset.quizSet
}

export default connect(mapStateToProps)(withRouter(ListQuizset))
