import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import AdminHeader from './AdminHeader'
import { fetchQuizset } from '../../actions'

class CreateQuiz extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  componentDidMount() {
    this.props.dispatch(fetchQuizset())
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value })
  }

  setQuizSet = event => {
    let index = event.target.value
    this.setState({
      quizset_id: this.props.allQuizset[index]._id,
      quizset: this.props.allQuizset[index].name
    })
  }

  handleSubmit = event => {
    event.preventDefault()

    if (
      !this.state.question ||
      !this.state.option1 ||
      !this.state.option2 ||
      !this.state.option3 ||
      !this.state.option4 ||
      !this.state.quizset||
      !this.state.quizset_id||
      !this.state.answer
    ) {
      alert('Please Fill The Form')
      return
    }

    fetch(`/api/v1/quizzes`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      },
      body: JSON.stringify({
        question: this.state.question,
        quizset: this.state.quizset,
        quizset_id: this.state.quizset_id,
        option1: this.state.option1,
        option2: this.state.option2,
        option3: this.state.option3,
        option4: this.state.option4,
        answer: this.state.answer,
        authorId: localStorage.id
      })
    })
    
      .then(res => res.json())
      .then(res => {
        if (res.msg) {
          alert(res.msg)
        }

        if (res.success) {
          this.props.history.push('/admin/quizzes')
        }
      })
  }

  render() {
    return (
      <>
        <AdminHeader />
        <div className='wrapper'>
          <h2 className='createnew'>Create New Quiz</h2>
          {/*  */}
          <form onSubmit={this.handleSubmit} className='create-quiz'>
            <label className='crate-new-label' htmlFor='question'>
              Question:
            </label>
            <input
              type='text'
              name='question'
              className='input-classic'
              onChange={this.handleChange}
            />
            <label className='crate-new-label' htmlFor='quizset'>
              Quizset:
            </label>
            <select
              name='quizset'
              onChange={this.setQuizSet}
              className='select-answer'
            >
              <option>Select</option>
              {this.props.allQuizset ? (
                this.props.allQuizset.map((val, ind) => (
                  <option value={ind} data-id={val._id} key={ind}>
                    {val.name}
                  </option>
                ))
              ) : (
                <option>Select</option>
              )}
            </select>
            <br />
            <label className='crate-new-label' htmlFor='option1'>
              Option1:
            </label>
            <input
              type='text'
              name='option1'
              className='input-classic'
              onChange={this.handleChange}
            />
            <label className='crate-new-label' htmlFor='option2'>
              Option2:
            </label>
            <input
              type='text'
              name='option2'
              className='input-classic'
              onChange={this.handleChange}
            />

            <label
              className='crate-new-label'
              htmlFor='option3'
              onChange={this.handleChange}
            >
              Option3:
            </label>
            <input
              type='text'
              name='option3'
              className='input-classic'
              onChange={this.handleChange}
            />
            <label className='crate-new-label' htmlFor='option4'>
              Option4:
            </label>
            <input
              type='text'
              name='option4'
              className='input-classic'
              onChange={this.handleChange}
            />
            <label className='crate-new-label' htmlFor='answer'>
              Answer:
            </label>

            <select
              name='answer'
              onChange={this.handleChange}
              className='select-answer'
            >
              <option value={this.state.option1}>{this.state.option1}</option>
              <option value={this.state.option2}>{this.state.option2}</option>
              <option value={this.state.option3}>{this.state.option3}</option>
              <option value={this.state.option4}>{this.state.option4}</option>
            </select>

            <button className='btn' type='submit'>
              Submit
            </button>
          </form>
        </div>
      </>
    )
  }
}

function mapStateToProps(state) {
  return state.qset.quizSet
}

export default connect(mapStateToProps)(withRouter(CreateQuiz))
